@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;300;400;500&display=swap');


.skillset__container{
    display: flex;
    flex-direction: column;
    font-family: 'Outfit', sans-serif;
    margin-bottom: 100px;
}

.skillset__box > h1{
    color: #00ADB5;
    font-size: 50px;
    font-weight: 300;
    text-align: center;
    margin-bottom: 50px;
}

.skillset__items{
    color: white;
    font-size: 20px;
    font-weight: 100;
    display: flex;
    flex-direction: column;
    border: 3px solid #00ADB5;
    border-radius: 10px;
    margin: 20px;
    padding: 10px 30px;
    width: 500px;
    
}  

.skillset__skills{
    box-sizing: border-box;
    padding: 10px;
    text-align: center;
}

.frontend, .backend, .database{
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-bottom: 30px;
}

.frontend >h1, .backend>h1, .database>h1{
    font-weight: 300;
    color: white;
}